.gallery-section {
  background-color: #56f;
  padding: 5px 0;
}

.search-form {
  width: 600px;
  margin: 0 auto;
  display: flex;
}

.search-input {
  height: 40px;
  border: 1px solid #aab4af;
  flex-grow: 1;
  padding-left: 15px;
  font-size: 18px;
}

.search-btn, .load-more {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #aab4af;
  border: none;
  padding: 10px 20px;
  font-size: 14px;
  transition: background-color .25s linear;
}

.load-more {
  margin: 10px auto;
  display: block;
}

.load-more.is-hidden {
  display: none;
}

.search-btn:hover, .search-btn:focus, .load-more:hover, .load-more:focus {
  background-color: #838885;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  margin-top: 30px;
  margin-bottom: 30px;
  display: flex;
}

.photo-card {
  border: 1px solid #000;
}

.info {
  justify-content: space-around;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.disabled, .disabled:hover {
  color: #505050;
  opacity: .8;
  background-color: #fac2c2;
}

